// TODO add typography.js
import React from "react";
import Container from "../components/container";

import profileImg from "../assets/images/profileImg.jpg";
import AddressCardIcon from "../components/addressCardIcon";
import FileCodeIcon from "../components/fileCodeIcon";
import ListAltIcon from "../components/listAltIcon";

import GithubIcon from "../components/githubIcon";
import LinkedInIcon from "../components/linkedInIcon";
import MediumIcon from "../components/mediumIcon";
import EnvelopIcon from "../components/envelopIcon";

import "./index.module.scss";
import navHeaderStyles from "./navHeader.module.scss";
import cardStyles from "./card.module.scss";
import aboutCardStyles from "./aboutCard.module.scss";
import experienceCardStyles from "./experienceCard.module.scss";
import footerStyles from "./footer.module.scss";

export default class App extends React.Component {
  person = {
    name: "Quang Nguyen",
    title: "Software Engineer"
  };

  contacts = [
    {
      key: "linkedin",
      tag: LinkedInIcon,
      url: "https://www.linkedin.com/in/qtnguyen18/"
    },
    {
      key: "github",
      tag: GithubIcon,
      url: "https://github.com/quangtn0018"
    },
    {
      key: "medium",
      tag: MediumIcon,
      url: "https://medium.com/@quangtn0018"
    },

    {
      key: "email",
      tag: EnvelopIcon,
      url: "mailto:quangtn0018@gmail.com"
    }
  ];

  cardIconProps = {
    width: 30,
    height: 30,
    fill: "lightgrey"
  };

  navIconProps = {
    width: 40,
    height: 40,
    fill: "white"
  };

  navItems = [
    {
      key: "home",
      tag: AddressCardIcon,
      isSelected: true
    },
    {
      key: "about",
      tag: ListAltIcon,
      isSelected: false
    },
    {
      key: "work experience/projects",
      tag: FileCodeIcon,
      isSelected: false
    }
  ];

  projects = [
    {
      key: "animalCollector",
      title: "AnimalCollector (Swift, Firebase, iOS)",
      url: "https://github.com/quangtn0018/AnimalCollector",
      description:
        "Let users take pictures of animals, compete with friends or other players to see who can collect the most animals and earn the highest scores"
    },
    {
      key: "pokedex",
      title: "Pokedex (Vue.js, Quasar, Express,js)",
      url: "https://github.com/quangtn0018/pokedex",
      description:
        "Pokedex using Vue.js, Quasar Framework, Express.js and data from pokeapi"
    },
    {
      key: "courseRecommender",
      title: "CourseRecommender (Swift, Firebase, iOS)",
      url: "https://github.com/quangtn0018/CourseRecommender",
      description:
        "Keeps track of and get recommended classes based on your current courses at CSUEB"
    }
  ];

  constructor(props) {
    super(props);

    const cardToRender = {
      isHome: true,
      isAbout: false
    };

    this.state = {
      cardToRender,
      navItems: this.navItems
    };
  }

  handleNavIconOnClick(e, key) {
    e.preventDefault();

    const cardToRender = {
      isHome: false,
      isAbout: false
    };

    let navItems = this.navItems.slice();

    switch (key) {
      case "home":
        cardToRender.isHome = true;
        navItems = navItems.map(item => {
          return {
            ...item,
            isSelected: item.key === "home"
          };
        });
        break;
      case "about":
        cardToRender.isAbout = true;
        navItems = navItems.map(item => {
          return {
            ...item,
            isSelected: item.key === "about"
          };
        });
        break;
      default:
        navItems = navItems.map(item => {
          return {
            ...item,
            isSelected: item.key === "work experience/projects"
          };
        });
    }

    this.setState({
      cardToRender,
      navItems
    });
  }

  render() {
    // TODO extract this to its own file
    const NavHeader = () => (
      <nav className={navHeaderStyles.navHeaderContainer}>
        <ul className={navHeaderStyles.ul}>
          {this.state.navItems.map(item => {
            const classnames = `${navHeaderStyles.icon} ${
              item.isSelected ? navHeaderStyles.selectedIcon : ""
            }`;
            // debugger
            return (
              <li
                tabIndex="0"
                className={classnames}
                onClick={e => this.handleNavIconOnClick(e, item.key)}
                key={item.key}
              >
                <item.tag iconProps={this.navIconProps} />
              </li>
            );
          })}
        </ul>
      </nav>
    );

    const HomeCard = () => (
      <div>
        <span className={cardStyles.avatar}>
          <img
            className={cardStyles.profileImg}
            src={profileImg}
            alt="avatar"
          />
        </span>
        <div className={cardStyles.titlesContainer}>
          <h1 className={cardStyles.name}>{this.person.name}</h1>
          <p className={cardStyles.jobTitle}>{this.person.title}</p>
        </div>
        <ul className={cardStyles.ul}>
          {this.contacts.map(item => (
            <a
              target="_blank"
              href={item.url}
              className={cardStyles.contact}
              key={item.key}
            >
              <item.tag iconProps={this.cardIconProps} />
            </a>
          ))}
        </ul>
      </div>
    );

    const AboutCard = () => (
      <div className={aboutCardStyles.aboutCardContainer}>
        <h1>About</h1>
        <div className={aboutCardStyles.aboutContainer}>
          {/* TODO needs styling */}
          <p className={aboutCardStyles.about}>
            I'm a full stack web developer living in the Bay Area! On my free
            time, I blog on{" "}
            <a target="_blank" href="https://medium.com/@quangtn0018">
              Medium
            </a>{" "}
            about things I have learned while working on software projects. I
            also keep up to date with the latest technologies by experimenting
            with personal projects on{" "}
            <a target="_blank" href="https://github.com/quangtn0018">
              GitHub
            </a>{" "}
            and watching tech talks.
          </p>
        </div>
      </div>
    );

    const ExperienceCard = () => (
      <div>
        <h1>Work Experience/Projects</h1>
        <div className={experienceCardStyles.projectsContainer}>
          {this.projects.map(project => {
            // TODO needs styling
            return (
              <div key={project.key} className={experienceCardStyles.projects}>
                <h2>
                  <a target="_blank" href={project.url}>
                    {project.title}
                  </a>
                </h2>
                <p>{project.description}</p>
              </div>
            );
          })}
        </div>
      </div>
    );

    const Card = () => (
      <section className={cardStyles.cardContainer}>
        <div className={cardStyles.card}>
          {!this.state.cardToRender.isHome &&
            !this.state.cardToRender.isAbout && <ExperienceCard />}
          {this.state.cardToRender.isHome && <HomeCard />}
          {this.state.cardToRender.isAbout && <AboutCard />}
        </div>
      </section>
    );

    const Footer = () => (
      <footer className={footerStyles.footer}>
        <p>
          © 2018 by{" "}
          <a target="_blank" href="https://quangtn0018.github.io">
            {this.person.name}
          </a>
        </p>
      </footer>
    );

    return (
      <Container>
        <NavHeader />
        <Card />
        <Footer />
      </Container>
    );
  }
}



